<template>
  <div id="soupon_regu">
    <div class="kckm_header">
      <span class="kckm_tit">我的代理</span>
      <div class="select_box select_box_none">
        <img src="/static/icon/4.png" style="width: 13px; margin-top: -7px; z-index:10;">
        <Select style="width:160px;position: absolute;z-index:10;top:0px" v-model="field">
          <Option v-for="item in field_select" :value="item.value" :key="item.value">{{item.title}}</Option>
        </Select>
        <Input placeholder="请输入对应的关键词" style="width:380px;position: relative;z-index:8;" v-model="val" />
      </div>
     <div class="search_some" @click.stop="cha">查询</div>
    </div>
    <div class="store_bottom">
      <table>
        <thead>
          <tr>
            <th>代理编号</th>
            <th>代理用户名</th>
            <th>分润盈利率</th>
            <th>代理状态</th>
            <th>操作</th>
          </tr>
        </thead>
        <tbody>
          <td v-if="list.length  == 0" colspan="11" align="center">
            <div class="none_now">
              <img src="/static/icon/7.png">
              <div>暂无相关数据</div>
            </div>
          </td>
          <tr v-else v-for="(item,index) in list" :key="item.Id" :class="typeof item.div =='undefined' ? '' : 'chaoz'">
            <td v-if="typeof item.div =='undefined'">{{item.Id}}</td>
            <td v-if="typeof item.div =='undefined'">{{item.phone}}</td>
            <td v-if="typeof item.div =='undefined'">{{item.share_rate}}%</td>
            <td v-if="item.status == 1 && typeof item.div =='undefined'">正常</td>
            <td v-if="item.status == 2 && typeof item.div =='undefined'">冻结</td>
            <td data-v-9154b70e="" v-if="typeof item.div =='undefined' && tdindex -1 != index" style="color: rgb(56, 108, 250); cursor: pointer; font-weight: bold;" @click="tdchange(index)" >操作</td>
            <td data-v-9154b70e="" v-if="tdindex - 1 == index" style="color: rgb(56, 108, 250); cursor: pointer; font-weight: bold;" @click="tdclose()" >关闭</td>
            <td colspan="5" class="chaoz_box"  v-if="typeof item.div !='undefined'" >
              <div @click.stop="ratego(item.data.Id)"><img src="/static/icon/16.png"><span>分润统计</span></div>
            </td>
          </tr>
        </tbody>
      </table>
      <Page :total="page.total" :page-size="15" show-total :current="page.current" class="fenye" v-if="list.length >= 1"
        @on-change="ajaxlist($event,field,val)" />
    </div>
    <Spin fix v-if="spin"></Spin>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        spin: true,
        list: [],
        field_select:[
          {
            value:"Id",
            title:"代理编号"
          },
          {
            value:"phone",
            title:"代理用户名"
          }
        ],
        field:"Id",
        val:"",
        page: {
          current: 1,
          total: ''
        },
        tdindex:"",

      }
    },
    mounted() {
      this.ajaxlist(1,this.field,this.val);
    },
    methods: {
      ajaxlist(page,field,val) {
        this.spin = true;
        this.$http.post('api/user/my_agent',{page:page,field:field,val:val}, res => {
              this.spin = false;
             if (res.status == 200) {
                 if(res.data.code == 200){
                  this.list = res.data.data.list;
                  this.page.current = res.data.data.current_page;
                  this.page.total = res.data.data.total;
                 }else if(res.data.code == 201){
                   this.$Message.warning(res.data.msg);
                 }
             } else {
                console.log(res.message);
             }
        });
      },
      tdclose(){
        if(this.tdindex != ""){
          this.list.splice(this.tdindex,1);
          this.tdstatus = false;
          this.tdindex = '';
        }
      },
      tdchange(i){
        if(this.tdstatus){
          this.list.splice(this.tdindex,1);
          var index = i >= this.tdindex ? i : i + 1;
        }else{
          this.tdstatus = true;
          var index = i + 1;
        }
        this.tdindex = index;
        var insert = {};
            insert.data = JSON.parse(JSON.stringify(this.list[i]));
            insert.div = true;
            insert.Id = 'div';
        this.list.splice(index,0,insert);
      },
      cha(){
        this.tdclose();
        this.ajaxlist(1,this.field,this.val);
      },
      ratego(id){
        this.$router.push({path:'/profit_statistics',params: { uid:id}});
      }
    }
  }
</script>



